 #wrapper {
  overflow-x: hidden;
  width:99vw;
  height: 100vh;
}
.nav-tabs{
display: flex;
justify-content:space-between;
margin-left:1rem;
margin-right: 1rem;
margin-top: 35px;
font-weight: bold;
}
#sidebar-wrapper {
display: grid;grid-template-rows: 80px 40px 1fr 60px;
min-width:289px;
height: 88.4vh;
width:25%;
overflow-x: auto;
margin-left: -15rem;
-webkit-transition: margin .25s ease-out;
-moz-transition: margin .25s ease-out;
-o-transition: margin .25s ease-out;
transition: margin .25s ease-out;
}

#sidebar-wrapper .sidebar-heading {
/* padding: 0.875rem 1.25rem; */
font-size: 1.2rem;
}
#sidebar-wrapper .sidebar-heading img{
width:100%;
}
#sidebar-wrapper .list-group {
width: 15rem;
}

#page-content-wrapper {
min-width: 100vw;
}

#wrapper.toggled #sidebar-wrapper {
margin-left: -21.5rem;
}

@media (min-width: 768px) {
#sidebar-wrapper {
  margin-left: 0;
}

#page-content-wrapper {
  min-width: 0;
  width: 100%;
}

/* #wrapper.toggled #sidebar-wrapper {
  margin-left: -16rem;
} */
}

@media (max-width: 768px) {
#sidebar-wrapper {
  margin-left: 0;
  display: grid;
  grid-template-rows: 80px 125px 1fr 60px;
}
.nav-tabs{
  display: inline;
}
.nav.nav-tabs {
  /* display: block;
  visibility: hidden; */
  max-width: 3em;
  
}
.nav-item{
  border-style: solid;
border-width: 1px;
border-color: antiquewhite;
margin-bottom: 3px;
}

/* .tab-selector:first-letter{
  visibility: visible;
} */

#page-content-wrapper {
  min-width: 0;
  width: 100%;
}

#wrapper.toggled #sidebar-wrapper {
  margin-left: -17.8rem;
}


}